.section-wedo {
  text-align: center;
  padding: 4.32395vw 1rem 4.5625rem; }
  @media only screen and (min-width: 768px) {
    .section-wedo {
      padding-bottom: 3.08854vw; } }
  .section-wedo .container {
    z-index: 2; }
  .section-wedo .section-heading {
    margin-bottom: 1.5625rem; }
    @media only screen and (min-width: 768px) {
      .section-wedo .section-heading {
        margin-bottom: 3.47222vw; } }

.wedo-block {
  display: inline-flex;
  display: flex;
  flex: 1 1 auto;
  flex-flow: row wrap;
  justify-content: center; }

.wedo {
  background-color: #fff;
  box-shadow: 0 0 15px 2px rgba(0, 0, 0, 0.13);
  border-radius: 45px;
  flex-basis: calc(100% - 1.75rem);
  margin: 0.5rem 0.875rem;
  padding: 0.875rem;
  max-width: 304px;
  min-height: 303px;
  width: 100%;
  /* &:hover {
    box-shadow: $wedo-block-shadow-hover;
  } */ }
  @media only screen and (min-width: 768px) {
    .wedo {
      flex-basis: calc(50% - 0.875rem); } }
  .wedo:first-of-type {
    border-bottom-left-radius: 0; }
  .wedo:nth-of-type(2) {
    border-bottom-right-radius: 0; }
  .wedo:nth-of-type(3) {
    border-top-left-radius: 0; }
  .wedo:nth-of-type(4) {
    border-top-right-radius: 0; }
  .wedo img {
    height: 50px;
    width: 50px; }
  .wedo h3 {
    color: #ff776f;
    font-size: 1.375rem;
    line-height: 130%;
    margin-top: 10px;
    margin-bottom: 1.125rem; }
    @media only screen and (min-width: 768px) {
      .wedo h3 {
        font-size: 1.3125rem; } }
  .wedo p {
    margin: 0 auto;
    font-size: 1.125rem;
    line-height: 130%; }

.hot-air-balloon-building {
  width: 188px;
  height: 188px;
  right: -57px;
  bottom: 253px;
  background-image: url(/img/hot-air-balloon-building.png);
  background-position: center;
  background-size: 188px 188px;
  background-repeat: no-repeat;
  transition: all 0.3s ease; }
  @media only screen and (min-width: 1024px) {
    .hot-air-balloon-building {
      right: 5.694444vw; } }
  @media only screen and (-o-min-device-pixel-ratio: 5 / 4), only screen and (-webkit-min-device-pixel-ratio: 1.25), only screen and (min-device-pixel-ratio: 1.25), only screen and (min-resolution: 1.25dppx) {
    .hot-air-balloon-building {
      background-image: url(/img/hot-air-balloon-building@2x.png); } }
  @media only screen and (-o-min-device-pixel-ratio: 9 / 4), only screen and (-webkit-min-device-pixel-ratio: 2.25), only screen and (min-device-pixel-ratio: 2.25), only screen and (min-resolution: 2.25dppx) {
    .hot-air-balloon-building {
      background-image: url(/img/hot-air-balloon-building@3x.png); } }

.hot-air-balloon {
  width: 109px;
  height: 111px;
  left: -1.5625rem;
  top: 185px;
  transition: all 0.3s ease; }
  @media only screen and (min-width: 1024px) {
    .hot-air-balloon {
      width: 248px;
      height: 196px; } }
  @media only screen and (min-width: 768px) {
    .hot-air-balloon {
      left: 1.73611vw;
      top: 196px; } }

.hot-air-balloon-building,
.hot-air-balloon {
  z-index: 1;
  position: absolute; }

